import React from "react";
// import "./header.css";
import { IoMdPersonAdd, IoIosLogIn} from "react-icons/io";
import logo from "../../images/tracemall.png";

import Button from "@material-ui/core/Button";
import AddIcon from '@material-ui/icons/Add';




class Nav extends React.Component {

    state = {
        navLinks: this.props.navLinks
    }

    listLinks = () => {
        const links = this.state.navLinks.map((link) => {
            return <li><a href={`#${link}`}>{link}</a></li>;
        });
        return <ul className="ul">{links}</ul>
    }

    render () {

        return (
            <nav className="nav">
                <Logo class="logo" image={logo} />
                {this.listLinks()}
                <Icons login="login" advert="advert" class="icons ripple" />
            </nav>
        )
    }
}

function Icons (props) {
    return (
        <div className="right-icons-container">
            <IoMdPersonAdd className={props.register, props.class} />
            <Button 
                variant="contained"
                color="primary"
                endIcon={<AddIcon />}
                className="ripple"
            >
                Post Add
            </Button>
        </div>
    )
}

function Logo (props) {
    return (
        <img src={props.image} className={props.class} />
    )
}

export default Nav;