import React from 'react';
import Select from 'react-select';
import data from "./data.json";
import { IoIosPin, IoIosBriefcase, IoIosCart } from "react-icons/io";
import Button from "@material-ui/core/Button";

export default function Intro () {
    return (
        <div className="intro">
            <div className="intro-text">
                <h1>Trade With Nearest Clients, Render a Service</h1>
                <h2>Hire Someone or Buy Anything</h2>
            </div>
            <div className="search-label">
                <span>Find Anything in</span>
                <span><IoIosPin /> Nigeria</span>
            </div>
            <Select
                className="search ripple"
                options={data}
            />

            <div className="call-to-action">
                <Button
                    variant="contained"
                    color="secondary"
                    size="large"
                    endIcon={<IoIosBriefcase />}
                    className="ripple action-btn"
                >
                    Hire
            </Button>
                <Button
                    variant="contained"
                    color="primary"
                    size="large"
                    endIcon={<IoIosCart />}
                    className="ripple action-btn"
                >
                    Shop Now
            </Button>
            </div>

        </div>
    );
}